        var dataArr = [
            4000,
            4200,
            3800,
            3500,
            4100,
            4150,
            4800,
            4000,
            3200,
            3600,
            3200,
            3100,
            4700,
            3500,
            4600,
            4500,
            3900,
            4700,
            3400,
